import React, { useRef, useState, useEffect } from 'react';
import { PanelProps } from '@grafana/data';
import mqtt from 'mqtt';

export interface Options {
  webrtcUrl: string;
  videoWidth: number;
  videoHeight: number;
  mqttLink: string;
  mqttTopic: string;
  regionPosition: string[];
}

export const SimplePanel: React.FC<PanelProps<Options>> = ({ options, width, height }) => {
  const { webrtcUrl, videoWidth, videoHeight, mqttLink, mqttTopic, regionPosition } = options;
  const [loaded, setLoaded] = useState(false);
  const [mqttMessage, setMqttMessage] = useState<string>('');
  const [mqttPreviousMessage, setMqttPreviousMessage] = useState<string>('');
  const iframeRef = useRef<HTMLIFrameElement>(null);
  // State for overlay rectangle: position and size
  const [overlayRect, setOverlayRect] = useState<{
    left: number;
    top: number;
    width: number;
    height: number;
    scale: number;
  }>({
    left: 0,
    top: 0,
    width: width,
    height: height,
    scale: 1,
  });
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    // Connect to a public MQTT broker via WebSocket (adjust URL as needed)
    const client = mqtt.connect(mqttLink);

    client.on('connect', () => {
      console.log('Connected to MQTT broker');
      // Subscribe to a specific topic
      client.subscribe(mqttTopic, (err) => {
        if (err) {
          console.error('Subscription error:', err);
        } else {
          console.log('Subscribed to ' + mqttTopic);
        }
      });
    });

    // Listen for messages on the subscribed topic
    client.on('message', (topic, payload) => {
      // Convert the payload to string and update the state
      setMqttMessage(payload.toString());
    });

    // Cleanup on unmount
    return () => {
      client.end();
    };
  }, [mqttLink, mqttTopic]);

  useEffect(() => {
    try {
      // Calculate the scale factor based on the panel's dimensions
      // This assumes the video is scaled with object-fit: contain
      const scale = Math.min(width / videoWidth, height / videoHeight);
      const displayedVideoWidth = videoWidth * scale;
      const displayedVideoHeight = videoHeight * scale;

      // Compute offsets (letterboxing) if the video doesn't fill the panel
      const left = (width - displayedVideoWidth) / 2;
      const top = (height - displayedVideoHeight) / 2;

      // Update the overlay rectangle so it only covers the displayed video area
      setOverlayRect({
        left,
        top,
        width: displayedVideoWidth,
        height: displayedVideoHeight,
        scale: scale,
      });
    } catch (error) {
      console.error('Error accessing iframe content:', error);
      // If there's an error (likely due to cross-origin restrictions),
      // fallback to covering the entire panel
      setOverlayRect({ left: 0, top: 0, width, height, scale: 1 });
    }
  }, [width, height, videoWidth, videoHeight]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Clear the canvas
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    // Set styling for the trapezoid
    ctx.strokeStyle = 'black';
    ctx.lineWidth = 2;

    // Define and draw the trapezoid shape
    // Points: top left (50,50), top right (150,50), bottom right (170,150), bottom left (30,150)
    regionPosition.map((items, index) => {
      const items_json = JSON.parse(items)
      var current_data = JSON.parse(mqttMessage);
      if (Object.keys(current_data).length == 0)
      {
        if (mqttPreviousMessage == '')
        {
          return;
        }
        current_data = JSON.parse(mqttPreviousMessage);
      }
      else
      {
        setMqttPreviousMessage(mqttMessage);
      }

      items_json.map((item: any, i: number) => {
        if (i == 0)
        {
          const matchingKeys = Object.keys(current_data).filter(key => key.startsWith(item.name + " "));
          ctx.fillStyle = 'rgba(0, 255, 0, 0.5)'; // Green with 50% opacity
          
          for (let i = 0; i < matchingKeys.length; i++) {
            if (current_data[matchingKeys[i]]["Status"] === "Occupied") {
              ctx.fillStyle = 'rgba(255, 0, 0, 0.5)'; // Red with 50% opacity
              break;
            }
          }
        }
        else if (i == 1)
        {
          ctx.beginPath();
          ctx.moveTo(overlayRect.left + item.x*overlayRect.scale, overlayRect.top + item.y*overlayRect.scale);
        }
        else
        {
          ctx.lineTo(overlayRect.left + item.x*overlayRect.scale, overlayRect.top + item.y*overlayRect.scale);
        }
      });
      ctx.closePath();
      ctx.fill();
      ctx.stroke();
    });
    
  }, [regionPosition, videoWidth, videoHeight, mqttMessage]);

  return (
    <div style={{ position: 'relative', width: '100%', height: '100%' }}>
      <iframe
        ref={iframeRef}
        src={webrtcUrl}
        title="WebRTC Video Stream"
        style={{ width: '100%', height: '100%', border: 'none', backgroundColor: 'black'}}
        allow="autoplay; encrypted-media"
        onLoad={() => setLoaded(true)}
      />
      
      {/* Overlay is only shown once the iframe has loaded */}
      {loaded && mqttMessage != '' && (
        <canvas
          ref={canvasRef}
          width={videoWidth}
          height={videoHeight}
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            pointerEvents: 'none', // Allows clicks to pass through to the iframe
            zIndex: 10, // Ensures the canvas is on top of the iframe
          }}
        />
      )}
    </div>
  );
};