import { PanelPlugin } from '@grafana/data';
import { Options, SimplePanel } from './components/SimplePanel';

export const plugin = new PanelPlugin<Options>(SimplePanel).setPanelOptions((builder) => {
  return builder
    .addTextInput({
      path: 'webrtcUrl',
      name: 'Video link',
      description: 'Description of video link',
      defaultValue: 'http://10.3.233.162:8082/?destination_peer_id=object_detection_1',
    })
    .addNumberInput({
      path: 'videoWidth',
      name: 'Video resolution width',
      description: 'Description of video resolution width',
      defaultValue: 3840,
    })
    .addNumberInput({
      path: 'videoHeight',
      name: 'Video resolution height',
      description: 'Description of video resolution height',
      defaultValue: 2160,
    })
    .addTextInput({
      path: 'mqttLink',
      name: 'MQTT link',
      description: 'Description of MQTT link',
      defaultValue: 'ws://10.3.233.162:9001/mqtt',
    })
    .addTextInput({
      path: 'mqttTopic',
      name: 'MQTT topic',
      description: 'Description of MQTT topic',
      defaultValue: 'object_detection_custom_total_1',
    })
    .addStringArray({
      path: 'regionPosition',
      name: 'Region position',
      description: 'Description of region position',
      defaultValue: ['[{"name": "Region 3"}, {"x": 1750, "y": 820}, {"x": 2200, "y": 800}, {"x": 2400, "y": 1100}, {"x": 1650, "y": 1150}]',
                     '[{"name": "Region 2"}, {"x": 1220, "y": 840}, {"x": 1680, "y": 830}, {"x": 1580, "y": 1150}, {"x": 860, "y": 1180}]'
      ]
    })
      // showIf: (config) => config.showSeriesCount,
});
